//Resource components========================================================
class ResourceDepot extends Component
{
	constructor(props)
	{
		super(props);
		
		this.state = {
			tally: Array(7).fill(0), //Bone, Hide, Organ, Scrap, Iron, Herb, Misc
			basicresources: JSON.parse(Const.BasicResources),
			whitelionresources: JSON.parse(Const.WhiteLionResources),
			vermin: JSON.parse(Const.Vermin),
			strangeresources: JSON.parse(Const.StrangeResources),
			screamingantaloperesources: JSON.parse(Const.ScreamingAntalopeResources),
			phoenixresources: JSON.parse(Const.PhoenixResources)
		};
		
		this.depotUpdate = this.depotUpdate.bind(this);
		this.showSelf = this.showSelf.bind(this);
	}
	
	showSelf()
	{
		if(this.props.display=="Resources")
			return {display: "block"};
		else
			return {display: "none"};
	}
	
	depotUpdate(group, index, tick)
	{
		//bone(0), hide(1), organ(2), scrap(3), iron(4), herb(5), misc(6)
		var tallyIndex, type, tmpArray, tmpObj;

		//update value of resource
		switch(group)
		{
			case "Basic Resources":
				//set state via index and set type to update tally
				tmpObj = this.state.basicresources;
				tmpObj.resources[index].value += tick;
				this.setState({basicresources: tmpObj});
				
				type = this.state.basicresources.resources[index].type;
				break;
			case "White Lion Resources":
				tmpObj = this.state.whitelionresources;
				tmpObj.resources[index].value += tick;
				this.setState({whitelionresources: tmpObj});
				
				type = this.state.whitelionresources.resources[index].type;
				break;
			case "Vermin":
				tmpObj = this.state.vermin;
				tmpObj.resources[index].value += tick;
				this.setState({vermin: tmpObj});
				
				type = this.state.vermin.resources[index].type;
				break;
			case "Strange Resources":
				tmpObj = this.state.strangeresources;
				tmpObj.resources[index].value += tick;
				this.setState({strangeresources: tmpObj});
				
				type = this.state.strangeresources.resources[index].type;
				break;
			case "Screaming Antalope Resources":
				tmpObj = this.state.screamingantaloperesources;
				tmpObj.resources[index].value += tick;
				this.setState({screamingantaloperesources: tmpObj});
				
				type = this.state.screamingantaloperesources.resources[index].type;
				break;
			case "Phoenix Resources":
				tmpObj = this.state.phoenixresources;
				tmpObj.resources[index].value += tick;
				this.setState({phoenixresources: tmpObj});
				
				type = this.state.phoenixresources.resources[index].type;
				break;
		}
		
		//update tally		
		switch(type)
		{
			case "bone":
				tallyIndex = 0;
				break;
			case "hide":
				tallyIndex = 1;
				break;
			case "organ":
				tallyIndex = 2;
				break;
			case "scrap":
				tallyIndex = 3;
				break;
			case "iron":
				tallyIndex = 4;
				break;
			case "herb":
				tallyIndex = 5;
				break;
			case "misc":
				tallyIndex = 6;
				break;
			default:
				tallyIndex = 0;
		}
		
		tmpArray = this.state.tally.slice();
		tmpArray[tallyIndex] = tmpArray[tallyIndex] + tick;
		this.setState({tally: tmpArray});
	}
	
	
	render()
	{
		return(
			<div className="kdapp_page" style={this.showSelf()}>
				<h2 className="kdapp_page_title">Resources</h2><hr/>
				<ResourceTally tally={this.state.tally} /><br/>
				<RB.PanelGroup accordion>
					<ResourceGroup template={this.state.basicresources} updatefc={this.depotUpdate} accordionid="1" />
					<ResourceGroup template={this.state.whitelionresources} updatefc={this.depotUpdate} accordionid="2" />
					<ResourceGroup template={this.state.vermin} updatefc={this.depotUpdate} accordionid="3" />
					<ResourceGroup template={this.state.strangeresources} updatefc={this.depotUpdate} accordionid="4" />
					<ResourceGroup template={this.state.screamingantaloperesources} updatefc={this.depotUpdate} accordionid="5" />
					<ResourceGroup template={this.state.phoenixresources} updatefc={this.depotUpdate} accordionid="6" />
				</RB.PanelGroup>
			</div>
		);
	}
}
