class ResourceGroup extends Component
{
	generateResource()
	{
		var resourceSection = this.props.template.resources.map((resource, i) =>
			<RB.FormGroup className="kdr-formgroup">
				<RB.Row>
					<RB.Col xs={4}><Resource group={this.props.template.title} index={i} value={resource.value} type={resource.type} updatefc={this.props.updatefc} /></RB.Col>
					<RB.Col componentClass={RB.ControlLabel} xs={6}>{resource.name}</RB.Col>
					<RB.Col componentClass={RB.ControlLabel} xs={2}>{resource.type}</RB.Col>
				</RB.Row>
				<RB.Row><RB.Col xs={12}>{resource.desc}</RB.Col></RB.Row>
			</RB.FormGroup>
		); 

		return resourceSection;
	}
	
	render()
	{
		return (
			<RB.Panel bsClass="kdr-panel panel" eventKey={this.props.accordionid}>
				<RB.Panel.Heading>
					<RB.Panel.Title toggle componentClass="h3"><b>{this.props.template.title}</b></RB.Panel.Title>
				</RB.Panel.Heading>
				<RB.Panel.Collapse>
					<RB.Panel.Body><RB.Grid>{this.generateResource()}</RB.Grid></RB.Panel.Body>
				</RB.Panel.Collapse>
			</RB.Panel>
		);
	}
}
class Resource extends Component
{
	constructor(props)
	{
		super(props);
		
		this.handleClickPlus = this.handleClickPlus.bind(this);
		this.handleClickMinus = this.handleClickMinus.bind(this);
	}
	
	handleClickMinus()	
	{
		if(this.props.value > 0)
			this.props.updatefc(this.props.group, this.props.index, -1);
	}
	
	handleClickPlus()
	{
		if(this.props.value < 99)
			this.props.updatefc(this.props.group, parseInt(this.props.index), 1);
	}
	
	render()
	{
		return(
			<RB.InputGroup className="kdr-inputgroup">
				<RB.InputGroup.Button>
					<RB.Button onClick={this.handleClickMinus}>-</RB.Button>
				</RB.InputGroup.Button>
				<RB.FormControl value={this.props.value} type="number" />
				<RB.InputGroup.Button>
					<RB.Button onClick={this.handleClickPlus}>+</RB.Button>
				</RB.InputGroup.Button>
			</RB.InputGroup>
		);
	}
}

