//Timeline components========================================================
class TimelineDepot extends Component
{
	constructor(props)
	{
		super(props);
		
		this.state = {
			timeline: JSON.parse(Const.Timeline),
			currentyear: Const.CurrentYear,
			buildings: JSON.parse(Const.SettlementLocations),
			innovations: Const.Innovations
		}
		
		this.showSelf = this.showSelf.bind(this);
		this.editEvent = this.editEvent.bind(this);
		this.gotoNextYear = this.gotoNextYear.bind(this);
		this.editInnovations = this.editInnovations.bind(this);
	}
	
	showSelf()
	{
		if(this.props.display=="Timeline")
			return {display: "block"};
		else
			return {display: "none"};
	}
		
	editEvent(index)
	{
		var tmpObj;
		var promptMsg = "Enter new event for year " + (index+1) + ":";
		var newCustomEvent = prompt(promptMsg, this.state.timeline.years[index].custom);
		
		if (newCustomEvent == null || newCustomEvent == "") 
		{
			alert("blah");
		} 
		else 
		{
			tmpObj = this.state.timeline;
			tmpObj.years[index].custom = newCustomEvent;
			this.setState({timeline: tmpObj});
		}
	}
	
	gotoNextYear(tick)
	{
		if((this.state.currentyear == 0) && (tick == -1))
			alert("Already at year 0");
		else if((this.state.currentyear == 29) && (tick == 1))
			alert("Already at year 30");
		else
			this.setState((prevState) => ({
			  currentyear: prevState.currentyear + tick
			}));
	}
	
	editInnovations(innovation)
	{
		
	}
	
	render()
	{
		return(
			<div className="kdapp_page" style={this.showSelf()}>
				<h2 className="kdapp_page_title">Timeline</h2><hr/>
				<Timeline timeline={this.state.timeline} currentyear={this.state.currentyear} updatefc1={this.editEvent} updatefc2={this.gotoNextYear}/>
				<RB.Grid>
					<RB.Row>
						<RB.Col xs={6}>
							<h3>Locations:</h3>
							<Buildings buildings={this.state.buildings} />
						</RB.Col>
						<RB.Col xs={6}>
							<h3>Quarries:</h3>
							<Buildings buildings={this.state.buildings} />
						</RB.Col>
					</RB.Row>
					<RB.Row>
						<RB.Col xs={12}>
							<h3>Innovations:</h3>
						</RB.Col>
					</RB.Row>
				</RB.Grid>
			</div>
		);
	}
}
class Timeline extends Component
{
	constructor(props)
	{
		super(props);
		
		this.generateTimeline = this.generateTimeline.bind(this);
		this.showActiveYear = this.showActiveYear.bind(this);
	}
	
	showActiveYear(index)
	{
		if(this.props.currentyear==index)
			return {backgroundColor: "red"};
	}
	
	generateTimeline()
	{
		var timelineSection = this.props.timeline.years.map((year, i) =>
			<RB.ListGroupItem style={this.showActiveYear(i)} className="kdt-listgroupitem" header={"Year " + (i + 1) + ":"}>
				<RB.Row>
					<RB.Col xs={6}><RB.Glyphicon glyph={year.type} /> {year.event}</RB.Col>
					<RB.Col xs={6}>{year.custom}<RB.Glyphicon onClick={() => this.props.updatefc1(i)} style={{float: "right"}} glyph="pencil" /></RB.Col>
				</RB.Row>
			</RB.ListGroupItem>
		); 

		return timelineSection;
	}
	
	render()
	{
		return(
			<div>
				<RB.Grid className="kdt_timepanel">
					<RB.ListGroup>{this.generateTimeline()}</RB.ListGroup>
				</RB.Grid>
				<hr/>
				<RB.Pager>
					<RB.Pager.Item previous onClick={() => this.props.updatefc2(-1)}>Prev Year</RB.Pager.Item>
					<RB.Pager.Item next onClick={() => this.props.updatefc2(1)}>Next Year</RB.Pager.Item>
				</RB.Pager>
			</div>
		);
	}
}
