//population components=====================================================
class PopulationDepot extends Component
{
	constructor(props)
	{
		super(props);
		
		var members = [this.props.size];
		
		if(this.props.members == "")
		{		
			for (var i = 0; i < this.props.size; i++)
			{
				members[i] = {name: "villager " + (i+1)};
			}
		}
		
		this.state = {
			size: this.props.size,
			members: members
		};
		
		this.showSelf = this.showSelf.bind(this);
		this.generatePopulation = this.generatePopulation.bind(this);
		this.nameChar = this.nameChar.bind(this);
	}
	
	showSelf()
	{
		if(this.props.display=="Population")
			return {display: "block"};
		else
			return {display: "none"};
	}
	
	nameChar(name, index)
	{
		var tmpObj;
		
		tmpObj = JSON.parse(Const.CharacterDefaults);
		tmpObj.name = name;
		//update state at index
	}
	
	generatePopulation()
	{
		var test;
		var populationSection = this.state.members.map((member, i) =>
			<RB.ListGroupItem className="kdc-listgroupitem" header={"Character Slot " + (i + 1) + ":"}>
				<RB.Row>
					<RB.Col xs={6}>Name: {member.name}</RB.Col>
					<RB.Col xs={6}><RB.Glyphicon onClick={() => this.nameChar(prompt("Enter Name", test))} style={{float: "right"}} glyph="pencil" /></RB.Col>
				</RB.Row>
			</RB.ListGroupItem>
		); 

		return populationSection;
	}
	
	render()
	{
		return(
			<div className="kdapp_page" style={this.showSelf()}>
				<h2 className="kdapp_page_title">Population</h2><hr/>
				<RB.Grid className="kdt_timepanel">
					<RB.ListGroup>{this.generatePopulation()}</RB.ListGroup>
				</RB.Grid>
			</div>
		);
	}
}
class CharacterSheet extends Component
{
	constructor(props)
	{
		super(props);
		
		var charvalue = JSON.parse(Const.CharacterDefaults);
		
		this.state = {
			name: charvalue.name,
			gender: charvalue.gender,
			survival: charvalue.survival,
			huntxp: charvalue.huntxp,
			weaponxp: charvalue.weaponxp,
			understanding: charvalue.understanding,
			courage: charvalue.courage,
			braindmg: charvalue.braindmg,
			stats: charvalue.stats,
			armor: charvalue.armor,
			fightingarts: "",
			disorders:"",
			abilities: ""			
		}
		
		this.showSelf = this.showSelf.bind(this);
		this.editTextField = this.editTextField.bind(this);
		this.generateAttribute = this.generateAttribute.bind(this);
		this.attributeUpdate = this.attributeUpdate.bind(this);
	}
	
	editTextField(field)
	{
		var promptMsg = "Edit entry:";
		var newValue, tmpObj;
		if(field=="fighting")
		{
			newValue = prompt(promptMsg, this.state.fightingarts);
		
			if (newValue !== null && newValue !== "") 
			{
				tmpObj = this.state.fightingarts;
				tmpObj = newValue + ", " + tmpObj
				this.setState({fightingarts: tmpObj});
			}
		}
		else if(field=="disorders")
		{
			newValue = prompt(promptMsg, this.state.disorders);
		
			if (newValue !== null && newValue !== "") 
			{
				tmpObj = this.state.disorders;
				tmpObj = newValue + ", " + tmpObj
				this.setState({disorders: tmpObj});
			}
		}
		else if(field=="abilities")
		{
			newValue = prompt(promptMsg, this.state.abilities);
		
			if (newValue !== null && newValue !== "") 
			{
				tmpObj = this.state.abilities;
				tmpObj = newValue + ", " + tmpObj
				this.setState({abilities: tmpObj});
			}
		}
	}
	
	showSelf()
	{
		if(this.props.display=="Character")
			return {display: "block"};
		else
			return {display: "none"};
	}
	
	attributeUpdate(type, index, tick)
	{
		var tmpObj;
		
		if(type=="armor")
		{
			if((this.state.armor[index].value > 0 && tick == -1) || (this.state.armor[index].value <= 99 && tick == 1))
			{
				tmpObj = this.state.armor;
				tmpObj[index].value += tick;
				this.setState({armor: tmpObj});
			}
		}
		else if(type=="stats")
		{
			if((this.state.stats[index].value > 0 && tick == -1) || (this.state.stats[index].value <= 99 && tick == 1))
			{
				tmpObj = this.state.stats;
				tmpObj[index].value += tick;
				this.setState({stats: tmpObj});
			}
		}
	}
	
	armorUpdate(type, index)
	{
		var tmpObj;
		
		if(type == "light")
		{
			tmpObj = this.state.armor;
			tmpObj[index].light = !this.state.armor[index].light;
			this.setState({armor: tmpObj});
		}
		else if(type == "heavy")
		{
			tmpObj = this.state.armor;
			tmpObj[index].heavy = !this.state.armor[index].heavy;
			this.setState({armor: tmpObj});
		}
		
	}
	
	generateAttribute(type)
	{
		var attributeSection;
		
		if(type=="armor")
		{
			attributeSection = this.state.armor.map((stat, i) =>
				<div>
					<h6>{stat.position}</h6>
					<div className="kdc-armor">{stat.value}</div>
					<div class="kdc-LH">
						<RB.Checkbox checked={stat.light} onClick={() => this.armorUpdate("light", i)}>L</RB.Checkbox>
						<RB.Checkbox checked={stat.heavy} onClick={() => this.armorUpdate("heavy", i)}>H</RB.Checkbox>
					</div>
					<RB.ButtonGroup>
						<RB.Button onClick={() => this.attributeUpdate("armor", i, -1)}>-</RB.Button>
						<RB.Button onClick={() => this.attributeUpdate("armor", i, 1)}>+</RB.Button>
					</RB.ButtonGroup>
				</div>
			); 	
		}
		else if(type=="stats")
		{
			attributeSection = this.state.stats.map((stat, i) =>
				<div>
					<h6>{stat.name}</h6>
					<div className="kdc-stats">{stat.value}</div>
					<RB.ButtonGroup>
						<RB.Button onClick={() => this.attributeUpdate("stats", i, -1)}>-</RB.Button>
						<RB.Button onClick={() => this.attributeUpdate("stats", i, 1)}>+</RB.Button>
					</RB.ButtonGroup>
				</div>
			); 	
		}
		
		return attributeSection;
	}
	
	render()
	{
		return(
			<div className="kdapp_page" style={this.showSelf()}>
				<h2 className="kdapp_page_title">Character: {this.state.name} ({this.state.gender})</h2><hr/>
				<RB.Grid className="kdc-sheet">
					<RB.Row>
						<RB.Col xs={6}>
							<RB.Panel bsClass="kdc-panel panel">
								<RB.Panel.Heading>
									<RB.Panel.Title componentClass="h3"><b>Stats:</b></RB.Panel.Title>
								</RB.Panel.Heading>
								<RB.Panel.Body>{this.generateAttribute("stats")}</RB.Panel.Body>
							</RB.Panel>
						</RB.Col>
						<RB.Col xs={6}>
							<RB.Panel bsClass="kdc-panel panel">
								<RB.Panel.Heading>
									<RB.Panel.Title componentClass="h3"><b>Armor:</b></RB.Panel.Title>
								</RB.Panel.Heading>
								<RB.Panel.Body>{this.generateAttribute("armor")}</RB.Panel.Body>
							</RB.Panel>							
						</RB.Col>
					</RB.Row>
					<hr/>
					<RB.Row>
						<RB.Panel bsClass="kdc-panel panel">
							<RB.Panel.Heading>
								<RB.Panel.Title toggle componentClass="h3"><b>Other Stats:</b></RB.Panel.Title>
							</RB.Panel.Heading>
							<RB.Panel.Collapse>
								<RB.Panel.Body>
									<RB.Col xs={6}>
										<h4>Survival</h4>
										<h4>Courage</h4>
										<h4>Understanding</h4>
									</RB.Col>
									<RB.Col xs={6}>
										<h4>Survival enabled</h4>
										<h4>Courage labels</h4>
										<h4>Understanding labels</h4>
									</RB.Col>
								</RB.Panel.Body>
							</RB.Panel.Collapse>
						</RB.Panel>			
					</RB.Row>
					<hr/>
					<RB.Row>
						<RB.Col xs={6}><h4>Fighting Arts:</h4></RB.Col>
						<RB.Col xs={12}>{this.state.fightingarts}<RB.Glyphicon onClick={() => this.editTextField("fighting")} style={{float: "right"}} glyph="pencil" /></RB.Col>
					</RB.Row>
					<RB.Row>
						<RB.Col xs={6}><h4>Disorders:</h4></RB.Col>
						<RB.Col xs={12}>{this.state.disorders}<RB.Glyphicon onClick={() => this.editTextField("disorders")} style={{float: "right"}} glyph="pencil" /></RB.Col>
					</RB.Row>
					<RB.Row>
						<RB.Col xs={6}><h4>Abilities:</h4></RB.Col>
						<RB.Col xs={12}>{this.state.abilities}<RB.Glyphicon onClick={() => this.editTextField("abilities")} style={{float: "right"}} glyph="pencil" /></RB.Col>
					</RB.Row>
				</RB.Grid>
			</div>
		);
	}
}

//menu components========================================================
class KDAppMenu extends Component
{
	showSettings (event) {
    //event.preventDefault();
  }

	render()
	{
		return(
			<RB.Navbar inverse collapseOnSelect>
				<RB.Navbar.Header>
					<RB.Navbar.Brand>
						<a onClick={()=> this.props.updatefc("Home")}>
							<span><img className="kdapp_logo" src={logo2} /><span className="kdapp_title">Kingdom Death App</span></span>
						</a>
					</RB.Navbar.Brand>
					<RB.Navbar.Toggle />
				</RB.Navbar.Header>
				<RB.Navbar.Collapse>
				<RB.Nav>
					<RB.NavItem eventKey={1} onClick={()=> this.props.updatefc("Character")}>Character Sheet</RB.NavItem>
					<RB.NavDropdown eventKey={2} title="Settlement" id="basic-nav-dropdown">
						<RB.MenuItem eventKey={2.1} onClick={()=> this.props.updatefc("Resources")}>Resources</RB.MenuItem>
						<RB.MenuItem eventKey={2.2} onClick={()=> this.props.updatefc("Inventory")}>Inventory</RB.MenuItem>
						<RB.MenuItem eventKey={2.3} onClick={()=> this.props.updatefc("Timeline")}>Timeline</RB.MenuItem>
						<RB.MenuItem eventKey={2.4} onClick={()=> this.props.updatefc("Population")}>Population</RB.MenuItem>						
					</RB.NavDropdown>
					<RB.NavItem eventKey={3} onClick={()=> this.props.updatefc("Notes")}>Game Notes</RB.NavItem>
				</RB.Nav>
				</RB.Navbar.Collapse>
			</RB.Navbar>
		);
	}
}
class HomeDepot extends Component
{
	constructor(props)
	{
		super(props);
		
		this.showSelf = this.showSelf.bind(this);
	}
	
	showSelf()
	{
		if(this.props.display=="Home")
			return {display: "block"};
		else
			return {display: "none"};
	}
		
	render()
	{
		return(
			<div style={this.showSelf()} className="kdh">
				<h1 className="kdapp_page_title">Homepage title</h1>
				<RB.ButtonGroup>
					<RB.Button>New Game</RB.Button>
					<RB.Button>Save Game</RB.Button>
					<RB.Button>Load Game</RB.Button>
				</RB.ButtonGroup>
			</div>
		);
	}
}

//application========================================================
class App extends Component 
{
	constructor(props)
	{
		super(props);
		
		this.state = {location: "Home"};
		
		this.updateLocation = this.updateLocation.bind(this);
	}

	updateLocation(target)
	{
		this.setState({location: target});
	}
	
	render()
	{
		return (
			<div class="KDApp">
				<KDAppMenu updatefc={this.updateLocation} />
				<HomeDepot display={this.state.location}  />
				<ResourceDepot display={this.state.location} />
				<InventoryDepot display={this.state.location} />
				<TimelineDepot display={this.state.location} />
				<PopulationDepot display={this.state.location} size="4" members="" />
				<CharacterSheet display={this.state.location} />
			</div>
		);
	}
}

export default App;



